import React from 'react';
import { useEffect, useState } from 'react';
import axios from 'axios';
import './Home.css';

const Home = () => {
  const images = [
    { src: "img1.jpg", title: " " },
    { src: "img2.jpg", title: " " },
    { src: "img3.jpg", title: " " }
  ]
  const [ inx, setInx ] = useState(0); //    
  const [ weather, setWeather ] = useState(null);

  //   -    
  useEffect(()=>{
    const slide = setInterval(()=>{
      setInx( prev => prev === images.length - 1 ? 0 : prev + 1 );
    }, 4000)
    return () => clearInterval(slide); //    . ? setInterval   ?  .   . 
  }, []);  // [] 


  useEffect(()=>{
    const API_KEY = '0f347a891b27262bf15023a506af4bbe'; //   
    const CITY = 'ansan';
    const URL = `https://api.openweathermap.org/data/2.5/weather?q=${CITY}&appid=${API_KEY}&units=metric`;

    const fetchWeather = async () => {
      try {
        const response = await axios.get(URL);
        console.log(response)
        setWeather(
          {
            temp: response.data.main.temp,
            description: response.data.weather[0].description,
            icon: response.data.weather[0].icon,
          }
        );
      } catch (err) {
        console.log(err);
      }
    }
    fetchWeather();
    
  }, [])


  return (
    <div className="home-container">
      {
        images.map((img, idx) => (
          <div
            key={idx} //    
            className={`img ${inx === idx ? 'active' : ''}`}
            style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/images/${img.src})` }} //${process.env.Public_URL}       . 
          >
          <h1 className={`title ${inx === idx? 'on' : ''}`}>
            {img.title}
          </h1>
          </div>
        ))
      }

      {/* api */}
      {
        weather && (
          <div className="weather">
            <img src={`http://openweathermap.org/img/wn/${weather.icon}@2x.png`} alt={weather.description}
            referrerPolicy='no-refferrer' //   ()
            />
            <div className="temp">{weather.temp}C</div>
            <div className="desc">{weather.description}</div>
          </div>
        )
      }
    </div>
  )
}

export default Home
