import React from 'react';
import { useEffect, useState } from 'react';
import './GuestBook.css';
import axios from 'axios'; //fetch   axios  .

const GuestBook = () => {

  const [ apiMessage, setApiMessage ] = useState([]); //get   (select  )
  const [ postData, setPostData ] = useState({ name: "", message: "" });  //      .  

  //select  
  const showMessage = async () => {
    try{
      //  
      const res = await axios.get('https://guest-391145246820.us-central1.run.app/guest');
      // const data = await res.json();
      setApiMessage(res.data);
      console.log(apiMessage);
    } catch(err) {
      console.error("  ",err);
    }
  }
  //     , 
  //showMessage();

  // ?
  useEffect(()=>{
    showMessage();
  },[apiMessage]); //    . . 



  //insert? post 
  const submitPost = async (e) => {
    e.preventDefault();
    try{
      //  
      await axios.post("https://guest-391145246820.us-central1.run.app/guest", postData);

      //input   
      setPostData({ name: "", message: "" });

      //    
      // await showMessage();
      // showMessage();
    } catch(err){
      console.error("  ",err);
    }
  }

  return (
    <div className="guest-container">
       {/* post   */}
       <form onSubmit={submitPost}>

          <h1 className="sub-title">GuestBook</h1>

          <div className="message">
          {
            apiMessage.map(data=>(
              <dl key={data.id} className="message-card">
                <dt> {data.name}<span>{new Date(data.created_at).toLocaleString()}</span></dt>
                <dd>{data.message}</dd>
              </dl>
            ))
          }
          </div>

          <div className="form">
            {/*  postData name  value  */}
            <input type="text" value={postData.name} placeholder='name'
            // **set  .**   . onChange   value setPostData .
            onChange={e => setPostData({ ...postData, name: e.target.value }) } //  name 
            required />
            
            <textarea 
              value={postData.message}
              onChange={e=> setPostData({ ...postData, message: e.target.value })}
              placeholder='message'
            ></textarea>

            <input type="submit" value="submit"/>
          </div>
       </form>

       
    </div>
  )
}

export default GuestBook
